import {
  Alert,
  AlertIcon,
  AlertTitle,
  AlertDescription,
} from "@chakra-ui/react";
import React from "react";

export const SecureWarning: React.FC = () => {
  return (
    <Alert status="error" w="100%" flexDirection="column" alignItems="center">
      <AlertIcon boxSize="2rem" />
      <AlertTitle mt="0.4rem" mb="0.2rem">
          
      </AlertTitle>
      <AlertDescription>
           . (HTTPS)   
          
      </AlertDescription>
    </Alert>
  );
};
